import { setAttribute, setComponentProps, createComponent } from '../react-dom/index'

export function diff(dom, vnode, container) {
    // ÂØπÊØîËäÇÁÇπÁöÑÂèòÂåñ
    const ret = diffNode(dom, vnode)
    if (container) {
        container.appendChild(ret)
    }
    return ret
}

export function diffNode(dom, vnode) {
    let out = dom
    if (vnode === undefined || vnode === null || typeof vnode === Boolean) return;
    // Â¶ÇÊûú vnode ÊòØÂ≠óÁ¨¶‰∏≤
    if (typeof vnode === 'number') vnode = String(vnode)
    if (typeof vnode === 'string') {
        if (dom && dom.nodeType === 3) {  // ÊñáÊú¨ËäÇÁÇπ
            if (dom.textContent !== vnode) {
                dom.textContent = vnode
            }
        }
        else {
            out = document.createTextNode(vnode)
            if (dom && dom.parentNode) {
                dom.parentNode.replaceNode(out, dom)
            }
        }
        return out
    }
    if (typeof vnode.tag === 'function') {
        return diffComponent(out, vnode)
    }
    // ÈùûÊñáÊú¨
    if (!dom) {
        out = document.createElement(vnode.tag)
    }
    if (vnode.childrens && vnode.childrens.length > 0 || (out.childNodes && out.childNodes.length > 0)) {
        // ÂØπÊØîÁªÑ‰ª∂ ÊàñËÄÖÂ≠êËäÇÁÇπ
        diffChildren(out, vnode.childrens)
    }
    diffAttribute(out, vnode)
    return out
}

function diffChildren(dom, vchildren) {  // element diff
    const domChildren = dom.childNodes
    const children = []
    const keyed = {}
    // Â∞ÜÊúâkeyÁöÑËäÇÁÇπÔºàÁî®ÂØπË±°‰øùÂ≠òÔºâÂíåÊ≤°ÊúâkeyÁöÑËäÇÁÇπÔºàÁî®Êï∞ÁªÑ‰øùÂ≠òÔºâÂàÜÂºÄ
    // if (domChildren.length > 0) { }
    if (vchildren && vchildren.length > 0) {
        let min = 0
        let childrenLen = children.length
        let newVchildren = [...vchildren]
        newVchildren.forEach((vchild, i) => {
            const key = vchild.key
            let child
            if (key) {
                if (keyed[key]) {
                    child = keyed[key]
                    keyed[key] = undefined
                }
            }
            else if (childrenLen > min) {
                for (let j = min; j < childrenLen; j++) {
                    let c = children[j]
                    if (c) {
                        child = c
                        children[j] = undefined
                        if (j === childrenLen - 1) childrenLen--
                        if (j === min) min++
                        break
                    }
                }
            }
            // ÂØπÊØî
            child = diffNode(child, vchild)
            // Êõ¥Êñ∞DOM
            const f = domChildren[i]
            if (child && child !== dom && child !== f) {
                // Â¶ÇÊûúÊõ¥Êñ∞ÂâçÁöÑÂØπÂ∫î‰ΩçÁΩÆ‰∏∫Á©∫ÔºåËØ¥ÊòéÊ≠§ËäÇÁÇπÊòØÊñ∞Â¢ûÁöÑ
                if (!f) {
                    dom.appendChild(child)
                }
                else if (child === f.nextSibling) {
                    removeNode(f)
                    // Â∞ÜÊõ¥Êñ∞ÂêéÁöÑËäÇÁÇπÁßªÂä®Âà∞Ê≠£Á°ÆÁöÑ‰ΩçÁΩÆ
                }
                else {
                    dom.insertBefore(child, f)
                }
            }
        })
    }
}


function diffAttribute(dom, vnode) {
    // ‰øùÂ≠ò‰πãÂâçÁöÑdomÁöÑÊâÄÊúâÂ±ûÊÄß
    let oldAttrs = {}
    let newAttrs = vnode.attrs
    // dom ÊòØÂéüÊúâÁöÑËäÇÁÇπÂØπË±°ÔºåvnodeËôöÊãüDOM
    let domAttrs = dom.attributes
    let newDomAttrs = [...domAttrs]
    newDomAttrs.forEach(item => {
        oldAttrs[item.name] = item.value
    })
    // ÂØπÊØîÔºåÂ¶ÇÊûúÂéüÊù•Â±ûÊÄßË∑üÊñ∞ÁöÑÂ±ûÊÄßÂØπÊØîÔºå‰∏çÂú®Êñ∞ÁöÑÂ±ûÊÄß‰∏≠ÔºåÂàôÂ∞ÜÂÖ∂ÁßªÈô§Êéâ
    for (let key in oldAttrs) {
        if (!(key in newAttrs)) {
            setAttribute(dom, key, undefined)
        }
    }
    // Êõ¥Êñ∞
    for (let key in newAttrs) {
        if (oldAttrs[key] !== newAttrs[key]) {
            setAttribute(dom, key, newAttrs[key])
        }
    }
}

function diffComponent(dom, vnode) {  // component diff
    // Â¶ÇÊûúÁªÑ‰ª∂Ê≤°ÊúâÂèòÂåñÔºåÈáçÊñ∞ËÆæÁΩÆprops
    let comp = dom
    if (comp && comp.constructor === vnode.tag) {
        // ÈáçÊñ∞ËÆæÁΩÆprops
        setComponentProps(comp, vnode.attrs)
        // ËµãÂÄº 
        dom = comp.base
    }
    else {
        //ÁªÑ‰ª∂Á±ªÂûãÂèëÁîüÂèòÂåñ
        if (comp) {
            // ÂÖàÁßªÈô§ÊóßÁöÑÁªÑ‰ª∂
            unmountComponent(comp)
            comp = null
        }
        // 1 ÂàõÂª∫Êñ∞ÁöÑÁªÑ‰ª∂
        comp = createComponent(vnode.tag, vnode.attrs)
        // 2 ËÆæÁΩÆÁªÑ‰ª∂Â±ûÊÄß
        setComponentProps(comp, vnode.attrs)
        // 3 ÁªôÂΩìÂâçÊåÇÂú®Base
        dom = comp.base
    }
    return dom
}

function unmountComponent(comp) {
    removeNode(comp.base)
}
